<template>
  <v-card class="overflow-hidden">
    <v-app-bar absolute color="white" elevate-on-scroll scroll-target="#scrolling-techniques-7">
      <v-toolbar-title>Profile</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-btn icon>
        <v-icon>mdi-magnify</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-heart</v-icon>
      </v-btn>

      <v-btn icon>
        <v-icon>mdi-dots-vertical</v-icon>
      </v-btn>
    </v-app-bar>
    <v-sheet
      id="scrolling-techniques-7"
      class="overflow-y-auto"
      max-height="600"
      style="margin-top: 64px;"
    >
      <v-container style="min-height: calc(100vh - 64px);height: auto;" class="px-0">
        <div class="d-flex" v-if="selectedUser">
          <v-col cols="6" class="profile-photo-container">
            <v-img :src="selectedUser.decodedProfileImage"></v-img>
            <v-file-input
              v-if="selectedUser._id === loggedUser._id"
              hide-input
              class="profile-photo-input"
              light
              prepend-icon="mdi-camera"
              @change="onSubmitProfilePhoto"
              v-model="profilePhoto"
            ></v-file-input>
          </v-col>
          <v-col cols="6">
            <h3>{{selectedUser.name}}</h3>
            <h4 class="text--secondary">@{{selectedUser.username}}</h4>
            <p>
              <b>{{selectedUser.follows.length}}</b> Following
            </p>
            <p>
              <b>{{selectedUser.followers.length}}</b> Followers
            </p>
          </v-col>
        </div>
        <v-card>
          <v-tabs background-color="white" color="blue">
            <v-tab>Posts</v-tab>
            <v-tab>Media</v-tab>

            <v-tab-item key="post">
              <v-container fluid>
                <PostList :posts="userPost" v-if="posts" />
              </v-container>
            </v-tab-item>
            <v-tab-item key="media">
              <v-container fluid>
                <v-row>
                  <v-col v-for="i in 6" :key="i" cols="12" md="4">
                    <v-img
                      :src="`https://picsum.photos/500/300?image=${i * 2 * 5 + 10}`"
                      :lazy-src="`https://picsum.photos/10/6?image=${i * 2 * 5 + 10}`"
                      aspect-ratio="1"
                    ></v-img>
                  </v-col>
                </v-row>
              </v-container>
            </v-tab-item>
          </v-tabs>
        </v-card>
      </v-container>
    </v-sheet>
  </v-card>
</template>

<script>
import PostList from "@/components/PostList";
import { mapGetters, mapActions } from "vuex";

export default {
  components: {
    PostList,
  },
  watch: {
    $route(to, from) {
      if (to != "home") {
        this.getUserByRoute();
      }
    },
  },
  data() {
    return {
      profilePhoto: null,
    };
  },
  computed: {
    ...mapGetters(["posts", "loggedUser", "selectedUser"]),
    userPost() {
      return this.posts.filter(
        (post) => post.creator._id == this.selectedUser._id
      );
    },
  },
  methods: {
    ...mapActions(["getPosts", "postProfilePhoto", "getProfile"]),
    onSubmitProfilePhoto() {
      let fd = new FormData();
      fd.append("profilePhoto", this.profilePhoto);
      this.postProfilePhoto(fd).then((res) => this.getUserByRoute());
    },
    getUserByRoute() {
      const res = this.getProfile(
        this.$router.currentRoute.path.replace("/", "")
      );
    },
  },
  created() {
    this.getUserByRoute();
    this.getPosts();
  },
};
</script>

<style lang="scss" scoped>
.profile-photo-container {
  position: relative;
  .profile-photo-input {
    position: absolute;
    bottom: 20px;
    right: -20px;
    color: white;
    .mdi-camera::before {
      content: "\F0100";
      color: rgb(6, 197, 255) !important;
    }
  }
}
</style>